.backdrop {
  width: 100%;
  height: 100%;
  z-index: 5;
  visibility: visible;
  background-color: #c8c8c833;
  justify-content: center;
  align-items: center;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
  overflow: visible;
}

.box-form {
  background-color: #c8c8c880;
  padding: 50px;
}

.form {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 30px;
  margin: auto;
  display: flex;
}

.notise {
  resize: none;
  background-color: beige;
  border-radius: 10px;
  padding: 10px;
  font-size: 22px;
  font-weight: 400;
}

button {
  color: #19440b;
  cursor: pointer;
  background-color: #14ff14b3;
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  font-size: 22px;
  font-weight: 400;
  box-shadow: 10px 10px 8px 2px #0000004d;
}

.controler {
  align-items: center;
  gap: 50px;
  display: flex;
}

.stikers-wall {
  border-top: 2px solid green;
  padding: 50px;
}

.box-notice {
  flex-wrap: wrap;
  justify-content: start;
  align-items: center;
  gap: 20px;
  display: flex;
}

.stikers:nth-child(3n) {
  background-color: #0ff;
  transform: rotate(.03turn);
}

.stikers:nth-child(2n) {
  background-color: red;
  transform: rotate(-.02turn);
}

.stikers:nth-child(4n) {
  background-color: #8a2be2;
  transform: rotate(-.04turn);
}

.stikers:nth-child(5n) {
  background-color: #7cfc00;
  transform: rotate(-.03turn);
}

.stikers {
  width: 200px;
  height: 200px;
  cursor: pointer;
  background-color: #ff69b4;
  padding: 10px;
  transition: transform .5s linear;
  transform: rotate(.01turn);
  box-shadow: 10px 10px 8px 2px #0000004d;
}

.stikers:hover {
  z-index: 2;
  position: relative;
  transform: scale(1.5);
}

.stikers.open {
  z-index: 5;
  position: fixed;
  top: 45%;
  left: 45%;
  transform: scale(2);
}

.stikers.yes {
  position: absolute;
  top: -100%;
  left: -100%;
}

.exit, .made {
  opacity: 1;
  padding: 10px;
  display: inline-block;
  overflow: visible;
}

.visually-hidden {
  opacity: 0;
  pointer-events: none;
  overflow: hidden;
}

